import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations';
import getters from './getters';
import actions from './actions';

Vue.use(Vuex);
const state = {
	latitude: '', // 
	longitude: '', // 
	cartList: {}, // 
	shopDetail: null, //
	userInfo: null, //
	shopid: null,//id
	remarkText: null,//
	inputText: '',//
	invoice: false,//
	newAddress: [], //
	searchAddress: null,//
	geohash: 'wtw3sm0q087',//geohash
	choosedAddress: null,//
	addressIndex: null,//
	needValidation: null,//
	cartId: null, //id
	sig: null,//sig
	orderParam: null,//
	orderMessage: null, //
	orderDetail: null, //
	login: true,//
	imgPath:null,//
	removeAddress:[],//
	addAddress:'',		//
	question: null,//
	cartPrice: null, //
	 cartList: localStorage.getItem('vuex_cart') ? JSON.parse(localStorage.getItem('vuex_cart')) : [],
    //
    cartInfos: {
        total_price: 0,
        total_nums: 0
    },
    //-1
    curIndex: -1
}
export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})


