 import {getproduct} from '../../../service/getData'
module.exports = {
    name: 'shop-list',
    data() {
        return {
          shop_list: ''
        }
    },
    mounted() {
        /**
         * 
         */
          this.initData();
        
      var list = this.$store.state.cart.cartList;
        for (var i = 0; i < this.shop_list.length; i++) {
            for (var j = 0; j < list.length; j++) {
                if (list[j].id == this.shop_list[i].fid) {

                    //state
                    // this.$set(this.shop_list[i], 'num', list[j].num);

                    //state,
                    this.$set(this.shop_list, i, list[j]);
                    break;
                }
            }
        }

        // console.log(this.shop_list[0].num);
        // console.log(this.shop_list[1].num);
    },
    methods: {
        /**
         * 
         */
        add_db(shop) {
            var id = shop.fid;

            //,state.curStatetrue,state.curIndex
            this.$store.dispatch('check_db', {
                id
            });

            //,num
            //push
            if (this.$store.state.curIndex != -1) {
                console.log('add_db');
                this.$store.dispatch('add_db');
            } else {
                console.log('create_db');
                this.$set(shop, 'num', 1);
                this.$store.dispatch('create_db', {
                    shop
                });
            }
        },

        /**
         * 
         */
        reduce_db(shop) {
            console.log(shop);
            var id = shop.fid;
            this.$store.dispatch('check_db', {
                id
            });
            shop.num = parseInt(shop.num);
            this.$store.dispatch('reduce_db');
        },async getdata(){
         this.$data.shop_list= await getproduct(1);
        console.log(this.$data.shop_list);
     },async initData(){
        var product;
        var items;
        product= await getproduct(1);
        this.shop_list=product.obj;
      
        }
    }
}


